<template>
  <div class="options-selection-time">
    <ul class="time-table">
      <li v-for="(item, index) in allYear" :key="index" class="clearfix">
        <span class="year-title">{{String(item[0].year).slice(0, 3) + '0s'}}</span>
        <div class="year-list">
          <Button :class="[i.selected?'active':'']" :disabled="!i.isShow" type="text" size="small" class="year-btns" v-for="i in item" :key="i.year" @click.native="chooseYear(i)">{{ i.year }}</Button>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props:['clearAll', 'choosedYearInfo'],
  data () {
    return {
      startY: 1980,
      allYear: {},
      valueYear: []
    }
  },
  created() {
    this.createdHandleYearList()
  },
  methods: {
    // 初始化处理年份列表
    createdHandleYearList() {
      let totalYear = [], arrYear = []
      // 起始年份-截止年份
      let startYear = this.startY
      let endYear = new Date().getFullYear()
      
      // 处理可点击年份
      for(let i = endYear; i >= startYear; i--) {
        totalYear.push({
          year: i,
          selected: false,
          isShow: true
        })
        // 获取所有年份的前三位，做去重处理
        arrYear.push(String(i).slice(0, 3))
        this.valueYear = new Set(arrYear)
      }
      // 处理不可点击年份
      const num = String(startYear).slice(3)
      const num1 = String(endYear).slice(3)
      const tempArr = []
      const tempArr1 = []
      // 补充开始年份之前
      for(let k = 0; k < num; k++) {
        tempArr.unshift({
          year: Number(String(startYear).slice(0, 3) + k),
          selected: false,
          isShow: false
        })
      }
      // 补充结束年份之后
      for(let j = 9; j > num1; j--) {
        tempArr1.unshift({
          year: Number(String(endYear).slice(0, 3) + j),
          selected: false,
          isShow: false
        })
      }
      totalYear = totalYear.concat(tempArr)
      totalYear = tempArr1.concat(totalYear)
      // 根据年份前三位重新组合年份数据
      this.valueYear.forEach(el => {
        const filterYear = totalYear.filter(i => String(i.year).slice(0, 3) === el)
        this.$set(this.allYear, el, filterYear.reverse())
      })
    },
    // 选择时间
    chooseYear(i) {
      const _flag = this.choosedYearInfo.indexOf(i)
      if(_flag > -1) {
        i.selected = false
        // 存在, 删除
        if(!i.selected){
          this.choosedYearInfo.splice(_flag, 1)
        }
      } else {
        i.selected = true
        // 不存在, 添加
        this.choosedYearInfo.push(i)
      }
      // console.log(this.choosedYearInfo)
      this.$emit('yearChange', this.choosedYearInfo)
    }
  },
  watch: {
    clearAll(){
      if(this.clearAll){
        this.createdHandleYearList()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.options-selection-time{
  box-sizing: border-box;
  padding: 20px 50px;
  .time-table{
    border: 1px solid #F2F2F2;
    height: 222px;
    box-sizing: border-box;
    padding: 10px;
    overflow: hidden;
    overflow-y: auto;
    li{
      height: 50px;
      box-sizing: border-box;
      padding: 0 20px;
      font-size: 16px;
      .year-title{
        display: inline-block;
        line-height: 50px;
        color: #4A4A4A;
        font-weight: 500;
        float: left;
        user-select: none;
      }
      .year-list{
        display: inline-block;
        line-height: 50px;
        text-align: right;
        width: 90%;
        float: right;
        .year-btns{
          border: none;
          font-size: 16px;
          margin-left: 30px;
          border-radius: 4px;
          &:focus{
            border: none;
            box-shadow: none;
          }
        }
        .active {
          background-color: #00aec4;
          color: #fff;
        }
        .year-btns:first-child{
          margin-left: 0;
        }
      }
    }
    li:nth-child(odd){
      background-color: #FBFBFB;
    }
    li:nth-child(even){
      background-color: #fff;
    }
  }
}
</style>





