<template>
    <section class="options-section">
        <div class="g-in options-generate">
            <div style="display: inline-block">
                <span class="generate-btn1" v-if="!countyIdLists.length || !choosedIndexs.length"></span>
                <span class="generate-btn" v-else @click="gotoCharts"></span>
                <!-- {{countyIdLists.length}}{{choosedIndexs.length}} -->
            </div>
            <span class="reset-btn" @click="resetAllData">重置所有选项</span>
        </div>
    </section>
</template>

<script>
import Cookies from 'js-cookie';
    export default {
        props: ['choosedIndexs', 'yearList', 'countyIdLists'],
        data() {
            return {
                indexIdLists: {}
            }
        },
        methods: {
            // 生成数据
            gotoCharts() {
                if (!this.choosedIndexs.length) {
                    this.$Message.error('请选择有效指标再进行操作！')
                    return
                }
                const indexIds = this.choosedIndexs.map(item => item.id)
                let params = {}
                if (!this.countyIdLists.length) {
                    this.$Message.error('请选择有效区县级城市再进行操作！')
                    return
                }
                params = {
                    years: this.yearList,
                    indexIds: indexIds,
                    cityIds: this.countyIdLists
                }
                Cookies.set('chartInfo', JSON.stringify(params))
                window.open(`#/charts`, '_blank')
                // this.$router.push({
                //     name: 'charts'
                // })
            },
            resetAllData() {
                this.$emit('resetAllData')
            }
        }
    }
</script>

<style scoped>
    .options-section{
        margin-bottom: 70px;
    }
    .options-section span {
        display: inline-block;
        width: 360px;
        height: 77px;
        vertical-align: middle;
        cursor: pointer;
    }
    .options-generate{
        text-align: right;
    }
    .options-section span+span {
        margin-left: 40px;
    }
    .options-section .reset-btn {
        background: #EDEDED;
        border: 1px solid #DCDCDC;
        border-radius: 4px;
        text-align: center;
        width: 182px;
        line-height: 77px;
        font-size: 22px;
        color: #666666;
        margin-left: 40px;
    }
    .generate-btn {
        background: url('../../../assets/image/generate.png') no-repeat;
        background-size: 100% 100%;
        line-height: 77px;
        font-size: 22px;
        color: #fff;
        text-align: center;
    }
    .generate-btn1 {
        background: url('../../../assets/image/un-generate.png') no-repeat;
        background-size: 100% 100%;
        line-height: 77px;
        font-size: 22px;
        color: #fff;
        text-align: center;
    }
    /* .operation-btn {
        background: url('../../../assets/image/analysis-btn.png') no-repeat;
        background-size: 100% 100%;
        line-height: 77px;
        font-size: 22px;
        color: #fff;
        text-align: center;
    } */
</style>



