<template>
  <div class="box">
    <selection-wrap ref="selectionWrap" @clearAllChoose="clearAllChoose" :yearList="yearList" :clearAll="clearAll" @yearChange="yearChange" :choosedIndexs="choosedIndexs" @countyIdList="countyIdList"></selection-wrap>
    <generate-section @resetAllData="resetAllData" :yearList="yearList" :choosedIndexs="choosedIndexs" :countyIdLists="countyIdLists"></generate-section>
  </div>
</template>

<script>
  import generateSection from './components/generate-btns'
  import selectionWrap from './components/selection-wrap'
  export default {
    components: {
      generateSection,
      selectionWrap
    },
    data () {
      return {
        choosedIndexs: [],
        clearAll: false,
        yearList: [],
        countyIdLists: [] 
      }
    },
    created() {
      this.yearList = this.createDefaultYear()
    },
    methods: { 
      // 设置默认年限列表
      createDefaultYear() {
        const firstYear = new Date().getFullYear() - 5
        const year = []
        for(let i = firstYear; i <= (firstYear + 5); i++) {
          year.push(i)
        }
        return year
      },
      // 年份列表更新
      yearChange(data){
        this.yearList.splice(0,this.yearList.length,...data);
      },
      // 区县级列表
      countyIdList(data) {
        this.countyIdLists = data
      },
      clearAllChoose() {
        this.choosedIndexs = []
        this.yearList = this.createDefaultYear()
      },
      resetAllData(){
        this.clearAll = true
        this.clearAllChoose()
        this.$refs['selectionWrap'].emitClearAllChoose()
        this.$router.replace({
          path: '/generatereset'
        })
      },

    },
    watch: {
      $route (to, from) {
        if(to.name==='generate'||to.name==='supple'){
          this.resetAllData()
        }
      }
    }
  }
</script>

<style scoped>
  .box {
    background: #f2f2f2;
    user-select: none;
    min-height: 700px;
  }
</style>



