<template>
  <Row :gutter="20" style="margin-top: 20px; line-height: 40px;">
    <Col span="5">
      <div>
        <img width="100%" src="../../../assets/image/map.png" alt="">
      </div>
      <p style="padding: 20px 10px; background: #F7F7F7; ">
        上海地处长江入海口，是长江经济带的龙头城市，隔东中国海与日本九州岛相望，南濒杭州湾，北、西与江苏、浙江两省相接。上海辖16个市辖区，总面积6340平方公里。
      </p>
    </Col>
    <Col span="19">
      <countys ref="county" :countyList="countyList" @countyIdList="countyIdList"></countys>
    </Col>
  </Row>
</template>
<script>
  import countys from './choose-county'
  export default {
    components: {
      countys
    },
    props: ['countyList'],
    data() {
      return {}
    },
    created() {
      
    },
    methods: {
      choosedCountyInfo(c){
        this.$refs['county'].choosedCountyInfo(c)
      },
      // 选中的区县级城市id
      countyIdList(data) {
        this.$emit('countyIdList', data)
      }
    }
  }
</script>




