<template>
  <div class="selected-options-wrap">
    <el-tree
      ref="tree"
      :data="dataTree"
      show-checkbox
      node-key="id"
      :check-strictly="true"
      :accordion="true"
      :props="defaultProps"
      @check="checkList">
    </el-tree>
  </div>
</template>

<script>
  export default {
    props: ['dataTree', 'choosedIndexs'],
    data() {
      return {
        // dataTree: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      }
    },
    methods: {
      // 点击节点
      checkList(val) {
        const _flag = this.choosedIndexs.indexOf(val)
        if(_flag > -1){
          // 存在
          this.$refs.tree.getNode(val).checked = false
          this.choosedIndexs.splice(_flag, 1)
        } else {
          // 不存在
          this.$refs.tree.getNode(val).checked = true
          this.choosedIndexs.push(val)
        }
        // console.log(this.choosedIndexs)
      }
    }
  }
</script>



