<template>
  <section class="select-section">
    <selection-card ref="selectionCard" @clearAllChoose="clearAllChoose" @yearChange="yearChange" :clearAll="clearAll" :yearList="yearList" :choosedIndexs="choosedIndexs" @countyIdList="countyIdList"></selection-card>
  </section>
</template>

<script>
import selectionCard from './selection-card'
export default {
  components: {
    selectionCard
  },
  props: ['choosedIndexs','clearAll','yearList'],
  methods: {
    // 传递年限
    yearChange(data){
      this.$emit('yearChange', data);
    },
    // 传递区县级城市id
    countyIdList(data) {
      this.$emit('countyIdList', data)
    },
    // 清空所有选择
    clearAllChoose() {
      this.$emit('clearAllChoose')
    },
    // 重置所有选项用
    emitClearAllChoose() {
      this.$refs['selectionCard'].clearAllChoose()
    }
  }
}
</script>

<style>
.select-section {
  overflow: hidden;
  box-sizing: border-box;
  padding: 40px 0 0 0;
}
</style>



