<template>
  <div>
    <div class="province-department">
      <div class="department-list">
        <span :class="[i.selected ?'active':'']" v-for="(i, index) in citys" :key="index+10" @click="clickCityInfo(i)">{{ i.cityName }}</span>
      </div>
      <Tabs class="county-tabs" type="card" :value="cityValue" closable @on-tab-remove="handleTabRemove">
        <TabPane :label="item.cityName" v-for="item in chooseCityList" :key="item.id+1000" :name="String(item.id)">
          <CheckboxGroup v-model="checkAllGroup" @on-change="checkAllGroupChange">
            <!-- <Checkbox :label="i.id" v-for="i in item.children" :key="i.id+2000">{{i.cityName}}</Checkbox> -->
            <Checkbox :class="[i.flag !== '1'?'un-chilck':'']" :label="i.id" v-for="i in item.children" :key="i.id+2000" :disabled="i.flag !== '1'">{{i.cityName}}</Checkbox>
          </CheckboxGroup>
        </TabPane>
      </Tabs>
    </div>
  </div>
</template>

<script>
export default {
  props: ['countyList'],
  data() {
    return {
      chooseCityList: [],
      citys: [],
      checkAllGroup: [], //选中的区县级的id
      cityValue: '',
    }
  },
  created() {
    this.hanldeData()
    // console.log(this.countyList)
  },
  methods: {
    // 处理数据
    hanldeData() {
      this.countyList.forEach(el => {
        this.$set(el, 'selected', false)
      })

      const firstLevel = this.countyList.filter(item => item.levelCode === '0') // 层级一
      const secondLevel = this.countyList.filter(item => item.levelCode === '1') // 层级二
      const threeLevel = this.countyList.filter(item => item.levelCode === '2') // 层级三
      firstLevel.forEach((el, index) => {
        if(!el.children){
            el.children = []
          }
        el.children.push(el)
      })
      // console.log(firstLevel)
      secondLevel.forEach((el, index) => {
        let self = {
          cityName: el.cityName,
          cityStatus: el.cityStatus,
          id: el.id,
          flag: el.flag,
          levelCode: el.levelCode,
          parentId: el.parentId,
        }
        if(!el.children){
          el.children = []
        }
        let newArr = threeLevel.filter(item => item.parentId === el.id)
        newArr.unshift(self)
        el.children = newArr
      })
      this.citys = [...firstLevel, ...secondLevel]
      // console.log(this.citys)
    },
    // 选择区县级城市
    checkAllGroupChange() {
      const list = this.checkAllGroup.map(item => {
        return this.countyList.filter(el => el.id === item)[0]
      })
      this.$emit("countyIdList", {idList: this.checkAllGroup, infoList: list})
    },
    // 选择市级
    clickCityInfo(i) {
      // this.chooseCityList
      const _flag = this.chooseCityList.indexOf(i)
      if(_flag > -1){
        // 存在
        i.selected = false
       this.clearAllCounty(i)
        this.chooseCityList.splice(_flag, 1)
      } else {
        // 不存在
        i.selected = true
        this.chooseCityList.push(i)
      }
    },
    // 删除已选地区，此方法被父级调用
    choosedCountyInfo(el) {
      this.checkAllGroup.splice(this.checkAllGroup.indexOf(el.id), 1)
    },
    // 清除城市下所有的区县选项
    clearAllCounty(data) {
      const childrenListId = this.chooseCityList[this.chooseCityList.indexOf(data)].children.map(item => item.id)
      // console.log(childrenListId)
      childrenListId.forEach((el, index, arr) => {
        if(this.checkAllGroup.includes(el)) {
          this.checkAllGroup.splice(this.checkAllGroup.indexOf(el), 1)
        }
      })
      this.checkAllGroupChange()
    },
    // 删除tabs市级指标，区县级勾选全部清空
    handleTabRemove(name) {
      const cy = this.chooseCityList.filter(item => item.id === Number(name))[0] // 数据对象
      // 取消选中
      this.chooseCityList[this.chooseCityList.indexOf(cy)].selected = false
      // 区县所有都取消选中
      this.clearAllCounty(cy)
      // 删除传递的指标
      this.chooseCityList.splice(this.chooseCityList.indexOf(cy), 1)
    }
  }
}
</script>

<style lang="scss" scoped>
.province-department{
  h4{
    font-weight: 700;
  }
  .department-list{
    span{
      background-color:#F3F7FF;
      border-radius:2px;
      padding: 4px 8px;
      margin-right: 10px;
      color: #00aec4;
      cursor: pointer;
      &.active{
        background-color: #00aec4;
        color: #fff;
      }
    }
  }
  .county-tabs{
    margin-top: 30px;
    .un-chilck{
      color: #aaa;
    }
  }
}
</style>



