<template>
  <div class="card-wrap">
    <div class="select-card g-in" v-for="i in 3" :key="i" v-if="flag">
      <div class="card-head clearfix">
        <div class="card-step-title">
          <span :class="[i===k.value ? 'active' : '', 'step-item']" v-for="k in options.stepMenu" :key="k.value"><span class="step-num">{{k.value}}</span>{{k.label}}</span>
        </div>
      </div>
      <div class="selected-options-wrap">
        <div class="filter-wrap">
          <div class="selected-options-row" v-if="i===1">
            <span class="selected-options-row-title">已选地区：</span>
            <div style="display: inline-block">
              <Tag closable color="default" v-for="c in choosedCountys" :key="c.id" @on-close="handleCloseCountyTag(c)">{{c.cityName}}</Tag>
            </div>
          </div>
          <div class="selected-options-row" v-if="i===2">
            <span class="selected-options-row-title">已选指标：</span>
            <Tag closable color="default" v-for="c in choosedIndexs" :key="c.value" @on-close="handleCloseIndexTag(c)">{{c.label}}</Tag>
          </div>
          <div class="selected-options-row" v-if="i===3">
            <span class="selected-options-row-title">已选年份：</span>
            <Tag closable color="default" v-for="c in choosedYearInfo" :key="c.year" @on-close="handleCloseYearTag(c)">{{c.year}}</Tag>
          </div>
        </div>
        <card1 ref="card1" :countyList="countyList" @countyIdList="countyIdList" v-if="i===1"></card1>
        <card2 ref="card2" :choosedIndexs="choosedIndexs" :dataTree="dataTreeInfo" v-else-if="i===2"></card2>
        <card3 ref="card3" @yearChange="yearChange" :clearAll="clearAll" :choosedYearInfo="choosedYearInfo" v-else></card3>
      </div>
    </div>
  </div>
</template>

<script>
  import card1 from './card-1.vue'
  import card2 from './card-2.vue'
  import card3 from './card-3.vue'
  import _ from 'lodash'
  export default {
    components: {
      card1,
      card2,
      card3
    },
    props: ['choosedCitys', 'choosedIndexs' ,'clearAll'],
    created() {
      this.getCounty()
      this.getParentListData()
    },
    data() {
      return {
        options: {
          stepMenu: [{
            label: '选择范围',
            value: 1
          }, {
            label: '选择指标',
            value: 2
          }, {
            label: '选择年份',
            value: 3
          }]
        },
        activeStep: 0,
        choosedYearInfo: [], // 已选年份信息
        showId: '', // 选择范围区分符
        countyList: [], //区县级信息
        choosedCountys: [], //已选区县级城市
        dataTreeInfo: [],
        flag: false // 数据请求结束后渲染组件
      }
    },
    methods: {
      // 清空搜索选项
      clearAllChoose() {
        this.choosedYearInfo = []
        this.choosedCountys = []
        this.$refs['card3'][0].createdHandleYearList()
        this.$emit('clearAllChoose')
      },
      // 获取区县级信息
      async getCounty() {
        const res = await this.$ajaxPost(this.projectName + '/city/findAll.do')
        // console.log(res)
        this.countyList = res
        this.flag = true
        // console.log(this.countyList)
      },
      // 获取指标体系属性图数据
      async getParentListData() {
        const res = await this.$ajaxPost(this.projectName + '/index/finds.do')
        // console.log(res)
        const parentFormatData = res.allList
        const allowReadList = res.allowReadList
        // console.log(parentFormatData)
        parentFormatData.forEach(el => {
          this.$set(el, 'label', el.name)
          this.$set(el, 'disabled', true)
          allowReadList.forEach(item => {
            if(item.nodeIds === el.nodeIds){
              this.$set(el, 'id', item.id)
              this.$set(el, 'disabled', false)
            }
          })
        })
        // 获得最低层级数
        const indexNumber = parentFormatData.map(item => item.nodeIds.split(',').length).sort().reverse()[0]
        const arr = []
        for(let a = 1; a<=indexNumber; a++){
          arr.push(parentFormatData.filter(item => item.nodeIds.split(',').length === a))
        }
        for(let j = 1; j<arr.length; j++){
          this.handleParentData(arr[j-1], arr[j])
        }
        this.dataTreeInfo = arr[0]
      },
      // 联级数据处理
      handleParentData(pre, next) {
        let str = ''
        return pre.forEach(ele => {
          next.forEach(k => {
            str = k.nodeIds.split(',').slice(0, k.nodeIds.split(',').length - 1).join(',')
            if(ele.nodeIds === str) {
              if(!ele.children){
                ele.children = []
              }
              ele.children.push(k)
            }
          })
        })
      },
      // 删除已选区县级城市
      handleCloseCountyTag(c) {
        // console.log(c)
        this.choosedCountys.splice(this.choosedCountys.indexOf(c), 1)
        this.$refs['card1'][0].choosedCountyInfo(c)
      },
      // 删除已选指标
      handleCloseIndexTag(c) {
        // console.log(c)
        this.$refs['card2'][0].checkList(c)
      },
      // 选中的区县级城市id
      countyIdList({idList, infoList}) {
        this.choosedCountys = infoList
        this.$emit('countyIdList', idList)
      },
      // 更新已选年份
      yearChange(data) {
        const yearInfo = data.map(item => item.year).sort() //获取年份排序
        this.$emit('yearChange', yearInfo);
      },
      // 时间处理，删除
      handleCloseYearTag(c) {
        // 调用子组件的方法
        this.$refs['card3'][0].chooseYear(c)
      }
    }
  }
</script>

<style scoped lang="scss">
  .select-card {
    margin-bottom: 20px;
  }
  .card-head {
    line-height: 52px;
    padding: 0 20px;
    border-radius: 6px 6px 0 0;
    background-color: #00aec4;
    .card-step-title {
      float: left;
      color: #fff;
      .step-item {
        color: rgba($color: #fff, $alpha: 0.5);
        .step-num {
          content: '1';
          display: inline-block;
          height: 20px;
          line-height: 20px;
          text-align: center;
          font-size: 14px;
          margin-right: 6px;
          width: 20px;
          border-radius: 20px;
          background-image: linear-gradient(-143deg, #FFD982 0%, #F8B31C 100%);
        }
        &+.step-item {
          margin-left: 20px;
          &:before {
            content: '';
            display: inline-block;
            width: 80px;
            margin-right: 15px;
            vertical-align: middle;
            border-bottom: 1px dashed rgba($color: #fff, $alpha: 0.5);
          }
        }
        &.active {
          color: #fff;
          font-size: 18px;
          vertical-align: top;
        }
      }
    }
  }
  .selected-options-wrap {
    background: #fff;
    width: 100%;
    padding: 20px;
    .filter-wrap {
      border-bottom: 1px solid #dfdfdf;
      padding-bottom: 10px;
    }
    .selected-options-row {
      line-height: 40px;
      .selected-options-row-title {
        padding-right: 20px;
      }
      .selected-options-row-item {
        padding: 6px 2px 6px 10px;
        background: #F1F1F1;
        border-radius: 2px;
        text-align: center;
        .close-icon {
          margin: 4px;
          padding: 2px;
          background: #DBDBDB;
          color: #fff;
          cursor: pointer;
          &:hover {
            color: #0C0E87;
            background: #fff;
          }
        }
      }
      .selected-options-row-item+.selected-options-row-item {
        margin-left: 10px;
      }
    }
  }
</style>



